.top-bar {
  padding-block: 24px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include onTablet {
    padding-block: 72px;
  }

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo-link {
    display: flex;
  }

  &__link-phone {
    display: block;
    text-decoration: none;
    color: $main-text-color;

    @include onDesktop {
      display: block;
    }

  }

  &__logo {
    height: 16px;

    @include hover(transform, scale(1.2));

    @include onTablet {
      height: 24px;
    }
  }
}
